<template>
  <b-overlay :show="property.isLoading" rounded="sm" :opacity="1">
    <div>
      <ReviewsModal
        :header-title="$t('common.landlord')"
        :reviews-data="propertyDetails.property.owner_review_question"
      />
      <div class="property-container">
        <b-container>
          <b-row>
            <b-col md="12">
              <b-row class="property-top-bar">
                <b-col md="6">
                  <b-button
                    class="back-btn d-none d-lg-block"
                    v-b-tooltip.hover
                    :title="$t('common.back')"
                    variant="text"
                    :to="
                      localePath(
                        `/properties?keyword=${propertyDetails.property.district}&term=0`
                      )
                    "
                  >
                    <svg-icon
                      name="blue-arrow-right"
                      class="arrow-custom"
                    ></svg-icon>
                  </b-button>
                  <b-button
                    class="back-btn d-flex align-items-center d-lg-none"
                    v-b-tooltip.hover
                    @click="back"
                    variant="text"
                  >
                    <svg-icon
                      name="blue-arrow-right"
                      class="arrow-custom"
                    ></svg-icon>
                    <Breadcrumbs :items="breadcrumbItems" />
                  </b-button>
                </b-col>
                <b-col md="6" class="icons d-none d-md-flex">
                  <SwitchInput
                    :value="!!propertyDetails.property.status"
                    :label="
                      !!propertyDetails.property.status
                        ? $t('property.vacant')
                        : $t('property.occupied')
                    "
                    @input="
                      changePropertyOpen(
                        $event,
                        propertyDetails.property.property_id
                      )
                    "
                    v-if="
                      user && user.user_id == propertyDetails.property.owner_id
                    "
                  />
                  <b-button
                    variant="link"
                    class="side-btn favourite-button"
                    @click="updateFavourite"
                    v-else
                  >
                    <svg-icon
                      name="grey-unchecked-heart"
                      :fill="propertyDetails.isFav ? '#fe4310' : '#333'"
                    />
                    {{ $t('common.favorite') }}
                  </b-button>
                </b-col>
                <b-col>
                  <div class="d-none d-lg-block">
                    <Breadcrumbs :items="breadcrumbItems" />
                  </div>
                  <PropertyOwnedBy
                    :owner-id="propertyDetails.property.owner_id"
                    :owner-name="propertyDetails.property.owner_name"
                    :rate="propertyDetails.property.owner_rating_avg"
                    :reviews-count="propertyDetails.property.owner_rating_count"
                  ></PropertyOwnedBy>
                </b-col>
              </b-row>
            </b-col>
            <b-col md="8" style="position: relative">
              <div class="actions-container d-md-none">
                <div
                  class="d-flex justify-content-between actions-subcontainer"
                >
                  <div class="side-btn">
                    <b-button
                      class="action-button d-inline-block"
                      @click="updateFavourite"
                    >
                      <svg-icon
                        class="action-icon"
                        name="grey-unchecked-heart"
                        :fill="propertyDetails.isFav ? '#fe4310' : '#333'"
                      />
                    </b-button>
                  </div>
                </div>
              </div>
              <PropertyGallery
                :images="propertyDetails.property.property_image"
              />
            </b-col>
            <b-col md="4">
              <PropertyBid
                :offerPrice="propertyDetails.property.offer_price"
                :property-price="propertyDetails.property.monthly_price"
                :property-daily-price="propertyDetails.property.daily_price"
                :property-security-deposite="
                  propertyDetails.property.security_deposite
                "
                :property-rate="propertyDetails.property.property_rating_avg"
                :property-reviews-count="
                  propertyDetails.property.property_rating_count
                "
                :property-name="propertyDetails.property.property_name"
                :property-term="propertyDetails.term"
                :property-id="propertyDetails.property.property_id"
                @onClick="goToPropertyReviews('#property-reviews-section')"
              />
            </b-col>

            <b-col cols="12">
              <PropertyDetails
                :name="this.propertyDetails.property.property_name"
                :address="propertyDetails.property.property_location_name"
                :number-of-bathroom="propertyDetails.property.no_of_bathroom"
                :number-of-bedroom="propertyDetails.property.no_of_bedroom"
                :area="propertyDetails.property.property_area"
              />
              <PropertyDescription
                :description="propertyDetails.property.description"
              />
              <PropertySleepingArrangements
                :bedrooms="getUnemptyBedrooms"
                v-if="getUnemptyBedrooms.length > 0"
              />
              <PropertyAmenities
                :amenities-categories="propertyDetails.property.amenities"
              />
              <PropertyNeighborhood
                :neighbour-review-data="
                  propertyDetails.property.neighborhood_review_question
                "
                :neighborhood-rating="
                  propertyDetails.property.neighborhood_rating_avg
                "
                :neighborhood-review-count="
                  propertyDetails.property.neighborhood_rating_count
                "
              />
              <PropertyReviews
                id="property-reviews-section"
                :property-reviews="propertyReviews"
                :reviews-data="
                  propertyDetails.property.property_review_question
                "
                :reviews-rating="propertyDetails.property.property_rating_avg"
                :reviews-count="propertyDetails.property.property_rating_count"
                :review-loading="property.reviewLoading"
                :number-reviews-shown="property.numOfReviewsShown"
                @viewAllReviews="viewAllReviews"
              />
            </b-col>
          </b-row>
        </b-container>
      </div>
      <PropertyBidDetails />
      <!-- ## Success modal -->
      <success-modal
        :title="$t('property.offerSentSucess')"
        :description="$t('property.successMessage')"
      ></success-modal>
    </div>
  </b-overlay>
</template>

<script>
import PropertySection from '@/components/property-details/PropertySection'
import PropertyDescription from '@/components/property-details/PropertyDescription'
import PropertySleepingArrangements from '@/components/property-details/PropertySleepingArrangements'
import PropertyAmenities from '@/components/property-details/PropertyAmenities'
import PropertyDetails from '@/components/property-details/PropertyDetails'
import PropertyReviews from '@/components/property-details/PropertyReviews'
import PropertyBid from '@/components/property-details/PropertyBid'
import PropertyGallery from '@/components/property-details/PropertyGallery'
import PropertyNeighborhood from '@/components/property-details/PropertyNeighborhood'
import SimilarProperties from '@/components/property-details/SimilarProperties'
import StarRateBox from '@/components/shared/StarRateBox'
import Breadcrumbs from '@/components/shared/Breadcrumbs'
import SendOfferModal from '~/components/favorite-properties/SendOfferModal'
import PropertyCompare from '@/components/property/PropertyCompare'
import CompareModal from '@/components/property/CompareModal'
import PropertyBidDetails from '~/components/property-details/PropertyBidDetails'
import ReviewsModal from '@/components/shared/ReviewsModal'
import SwitchInput from '@/components/shared/SwitchInput.vue'
import EditProperty from '@/components/shared/EditProperty'
import PropertyOwnedBy from '@/components/property-details/PropertyOwnedBy'
import { scrollToElement } from '@/helpers/common'
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('PropertyModule')
const { mapState: mapUserState } = createNamespacedHelpers('UserModule')

export default {
  components: {
    PropertySection,
    PropertyDescription,
    PropertySleepingArrangements,
    PropertyAmenities,
    PropertyDetails,
    PropertyReviews,
    PropertyBid,
    PropertyGallery,
    PropertyNeighborhood,
    StarRateBox,
    Breadcrumbs,
    SimilarProperties,
    SendOfferModal,
    PropertyCompare,
    CompareModal,
    PropertyBidDetails,
    ReviewsModal,
    SwitchInput,
    EditProperty,
    PropertyOwnedBy,
  },
  data() {
    return {
      loading: false,
      shareUrlfb: `https://www.facebook.com/sharer/sharer.php?u=`,
      shareUrltwitter: `https://twitter.com/intent/tweet?url=`,
      screenWidth: 0,
      openSide: 0,
      compareArray: [],
    }
  },
  fetch({ store: { dispatch }, params, query }) {
    let content = {
      propertySlug: params.id,
      term: query.term == 1 ? 1 : 0,
    }
    return dispatch('PropertyModule/loadPropertyData', content)
  },
  computed: {
    ...mapState([
      'propertyDetails',
      'property',
      'propertyReviews',
      'compareProperty',
      'reviewQuestions',
    ]),
    ...mapUserState(['user']),
    isRTL() {
      return this.$store.getters['CoreModule/isRTL']
    },
    fullUrl() {
      return typeof location !== 'undefined' && location.href
    },
    domainName() {
      return typeof location !== 'undefined' && location.origin
    },
    urlFacebook() {
      return this.shareUrlfb + this.fullUrl
    },
    urlTwittter() {
      return this.shareUrltwitter + this.fullUrl
    },
    breadcrumbItems() {
      if (this.screenWidth < 768) {
        return [
          {
            text: this.propertyDetails.property.city,
            active: true,
          },
        ]
      } else {
        return [
          {
            text: this.propertyDetails.property.city,
            to: this.localePath(
              `/properties?keyword=${this.propertyDetails.property.city}&term=${this.$route.query.term}`
            ),
          },
          {
            text: this.propertyDetails.property.district,
            to: this.localePath(
              `/properties?keyword=${this.propertyDetails.property.district}&term=${this.$route.query.term}`
            ),
          },
          {
            text: this.propertyDetails.property.neighbourhood,
            to: this.localePath(
              `/properties?keyword=${this.propertyDetails.property.neighbourhood}&term=${this.$route.query.term}`
            ),
          },
          {
            text: this.propertyDetails.property.property_location_name,
            active: true,
          },
        ].filter((item) => {
          return item.text
        })
      }
    },
    getUnemptyBedrooms() {
      return (
        (this.propertyDetails.property.bed_types || {}).bedrooms || []
      ).filter((currentBedroom) => currentBedroom.types.length > 0)
    },
  },
  mounted() {
    this.getPropertyReviews({
      property_id: this.propertyDetails.property.property_id,
      per_page: this.property.numOfReviewsShown,
    })
    if (process.browser) {
      this.setScreenWidth()
      window.addEventListener('resize', this.setScreenWidth)
    }
  },
  watch: {
    showWarningToaster(newVal) {
      if (newVal) {
        setTimeout(() => {
          this.hideWarningToaster()
        }, 3000)
      }
    },
    compareArray() {
      this.getCompareProperty(this.compareArray)
    },
  },
  methods: {
    ...mapActions([
      'updatePropertyFavourite',
      'hideWarningFavouriteModal',
      'hideWarningToaster',
      'getPropertyReviews',
      'getCompareProperty',
      'showReviewModal',
      'getLandlordQuestions',
      'updatePropertyOpen',
    ]),
    updateFavourite() {
      let formdata = {
        prop_id: this.propertyDetails.property.property_id,
        term: this.propertyDetails.term,
        type: 'single-property',
      }
      this.updatePropertyFavourite(formdata)
    },
    onMouseOver() {
      this.$refs.dropdown.visible = true
    },
    onMouseLeave() {
      this.$refs.dropdown.visible = false
    },
    setScreenWidth() {
      this.screenWidth = window.innerWidth
    },
    viewAllReviews(viewLess) {
      this.getPropertyReviews({
        property_id: this.propertyDetails.property.property_id,
        ...(viewLess.viewLess
          ? { per_page: this.property.numOfReviewsShown }
          : {}),
      })
    },
    updateCompare(data) {
      if (
        this.compareArray.length < 4 &&
        this.compareArray.includes(data) == false
      ) {
        this.compareArray.push(data)
      }
      this.openSide++
    },
    clearCompareData() {
      this.compareArray = []
    },
    deleteDetectedItem(property_id) {
      let index = this.compareArray.findIndex((item) => item === property_id)
      this.compareArray.splice(index, 1)
    },
    compareCurrent() {
      let id = this.propertyDetails.property.property_id
      this.updateCompare(id)
    },
    back() {
      this.$router.back()
    },
    changePropertyOpen(status, id) {
      this.updatePropertyOpen({ id, status: +status, type: 'propertyDetails' })
    },
    goToPropertyReviews(hash) {
      scrollToElement(hash)
    },
  },
  head() {
    return {
      title: `property in  ${this.$route.params.id}`,
    }
  },
}
</script>

<style lang="scss" scoped>
.property-container {
  .property-top-bar {
    padding: 20px 0;
    align-items: baseline;

    .icons {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      align-items: center;
      color: $mainTextColor;
      font-size: 14px;
      .side-btn {
        margin-left: 24px;
        [dir='rtl'] & {
          padding-right: 24px;
          padding-left: 0;
          margin-left: 0;
        }
      }
      .share-dropdown {
        font-size: 14px;
        background: none;
        border: 0;
        color: $mainTextColor;
      }
      .favourite-button {
        font-size: 14px;
        color: $mainTextColor;
        margin-bottom: 0;
      }
      .compare-btn {
        width: 170px;
        height: 45px;
        background-color: white;
        border-radius: 5px;
        border: 1px solid $mainColor;
        color: $mainColor;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .compare-icon {
          width: 20px;
          height: 14px;
        }
      }
    }
    svg {
      width: 18px;
      height: 18px;
    }
  }
}
.actions-container {
  position: absolute;
  top: 15px;
  right: 30px;
  left: 30px;
  z-index: 9;
  .actions-subcontainer {
    align-items: center;
  }
  .action-button {
    width: 28px;
    padding: 1px;
    height: 28px;
    background-color: white;
    border-radius: 5px;
    border: none;
    color: $mainTextColor;
    display: flex;
    justify-content: center;
    align-items: center;
    .action-icon {
      width: 18px;
      height: 18px;
    }
  }
}
.owner-mobile-name {
  font-size: 14px;
  text-decoration: underline;
}
::v-deep() .breadcrumb {
  padding: 0;
}
.back-btn {
  background: none;
  border: none;
  padding: 0;
}
.arrow-custom {
  width: 30px !important;
  height: 30px !important;
  transform: rotate(180deg);
  [dir='rtl'] & {
    transform: rotate(360deg);
  }
}
</style>
