      </div>
    </template>
  </PropertySection>
</template>
<style lang="scss" scoped>
.owner-name {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: $mainTextColor;

  .owner-by {
    padding-right: 5px;
    [dir='rtl'] & {
      padding-left: 5px;
      padding-right: 0px;
    }
  }
  .name {
    color: $mainColorLight;
    padding-right: 12px;
    cursor: pointer;
    [dir='rtl'] & {
      padding-left: 12px;
      padding-right: 0px;
    }
  }
  @include media-breakpoint-down(sm) {
    justify-content: space-between;
  }
}
.property-section {
  @include media-breakpoint-down(sm) {
    margin-bottom: 17px;
    padding-bottom: 15px;
  }

  @include media-breakpoint-up(md) {
    border: none;
    padding-bottom: 0px;
    margin-bottom: 0px;
  }
}
</style>
<script>
import PropertySection from '@/components/property-details/PropertySection'
import StarRateBox from '@/components/shared/StarRateBox'
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('PropertyModule')
const { mapState: mapUserState } = createNamespacedHelpers('UserModule')
export default {
