<template>
  <div class="similar-properties">
    <b-container>
      <b-row>
        <b-col md="12">
          <PropertySection>
            <template v-slot:title>
              {{ $t('property.propertyDetails.similarPropertiesTitle') }}
            </template>

            <template>
              <b-overlay :show="similarProperties.loading">
                <swiper
                  class="swiper gallery-top"
                  :options="swiperOption"
                  ref="mySwiper"
                >
                  <swiper-slide
                    v-for="(row, index) in similarProperties.data
                      .nearbyproperty"
                    :key="index"
                  >
                    <property-card
                      @update="updateCompare($event)"
                      :card-data="row"
                      @showSendOfferModal="handleOffer"
                      @updateFavouriteProperty="updateFavourite"
                    />
                  </swiper-slide>
                </swiper>
              </b-overlay>
              <b-row>
                <b-col class="pagination_buttons">
                  <div
                    class="
                      swiper-nav-button
                      swiper-button-prev
                      swiper-similar-button-prev
                      swiper-button-white
                    "
                    slot="button-prev"
                  ></div>
                  <div
                    class="swiper-similar-pagination"
                    slot="pagination"
                  ></div>
                  <div
                    class="
                      swiper-nav-button
                      swiper-button-next
                      swiper-similar-button-next
                      swiper-button-white
                    "
                    slot="button-next"
                  ></div>
                </b-col>
              </b-row>
            </template>
          </PropertySection>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import PropertySection from '@/components/property-details/PropertySection'
import { Swiper, SwiperSlide, directive } from 'vue-awesome-swiper'
import PropertyCard from '~/components/shared/PropertyCard'
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('PropertyModule')
export default {
  name: 'swiper-example-multiple-slides-per-biew',
  title: 'Multiple slides per view',
  components: {
    Swiper,
    SwiperSlide,
    PropertyCard,
    PropertySection,
  },
  data() {
    return {
      swiperOption: {
        slidesPerView: 3,
        spaceBetween: 30,
        slidesPerGroup: 3,
        pagination: {
          el: '.swiper-similar-pagination',
          clickable: true,
        },
        navigation: {
          nextEl: '.swiper-similar-button-next',
          prevEl: '.swiper-similar-button-prev',
        },
        breakpoints: {
          1024: {
            slidesPerView: 3,
            spaceBetween: 30,
            slidesPerGroup: 3,
          },
          768: {
            slidesPerView: 2,
            spaceBetween: 20,
            slidesPerGroup: 2,
          },
          320: {
            slidesPerView: 1,
            spaceBetween: 20,
            slidesPerGroup: 1,
          },
        },
      },
      compareArray: [],
    }
  },
  computed: {
    ...mapState(['similarProperties', 'propertyDetails']),
  },
  mounted() {
    let content = {
      property_id: this.propertyDetails.property.property_id,
      property_slug: this.$route.params.id,
    }
    this.getSimilarProperties(content)
  },
  methods: {
    ...mapActions([
      'updatePropertyFavourite',
      'showSendOfferModal',
      'getSimilarProperties',
    ]),
    handleOffer(offer) {
      this.showSendOfferModal({ offerData: offer })
      const offerCard = document.getElementById('send-offer-mobile-view')
      offerCard.classList.remove('fixed-offer-card')
    },
    updateFavourite(data) {
      let formdata = {
        prop_id: data.property_id,
        term: data.term,
        type: 'similar-properties',
      }
      this.updatePropertyFavourite(formdata)
    },
    updateCompare(id) {
      this.$emit('update', id)
    },
  },
}
</script>

<style lang="scss" scoped>
.similar-properties {
  background-color: $grayBackgroundColor;
  .property-section {
    padding-top: 28px;
    padding-bottom: 10px;
    margin-bottom: 0;
  }
  .property-section {
    border: 0px;
  }
  ::v-deep() {
    .swiper-pagination-bullet-active {
      border: 1px solid $secondaryTextColor;
      background-color: #ffffff;
      box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.08);
    }
    .swiper-pagination-bullet {
      width: 11px;
      height: 11px;
      margin: 0 5px;
    }
  }
  .swiper-nav-button {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;

    color: #000;
    background-color: #fff;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.47);
    font-style: 12px;
    &:after {
      font-size: 14px;
      font-weight: 700;
    }
  }
  .swiper-similar-pagination {
    position: static;
    padding: 0 20px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
    margin-top: 0px;
  }
  .swiper-button-next:after,
  .swiper-container-rtl .swiper-button-prev:after {
    [dir='rtl'] & {
      transform: rotate(180deg);
    }
  }
  .swiper-button-prev:after,
  .swiper-container-rtl .swiper-button-next:after {
    [dir='rtl'] & {
      transform: rotate(180deg);
    }
  }
  .pagination_buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    padding: 20px;
  }
}
</style>
