<template>
  <div class="p-0 section-container">
    <div>
      <div class="label rating-label">{{ ratingLabel }}</div>
    </div>
    <div class="rate-container">
      <StarRating :value="Number(ratingAvg)" v-if="ratingAvg" />
      <div class="rate-percent" v-if="ratingAvg">
        {{ ratingAvg | toDecimal }}
      </div>
      <div class="rate-percent" v-else style="font-size: 14px">
        {{ $t('property.noRatingYet') }}
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.rating-label {
  max-width: 280px !important;

  @include media-breakpoint-down(sm) {
    max-width: 220px !important;
  }
}

.section-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 24px;
  justify-content: space-between;
  .rate-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    .rate-percent {
      margin-left: 10px;
      font-size: 14px;
      font-weight: 600;
      line-height: normal;
      [dir='rtl'] & {
        margin-right: 10px;
        margin-left: 0px;
      }
    }
  }
  .label {
    font-size: 14px;
  }
}
</style>
<script>
import StarRating from '@/components/shared/StarRating'
export default {
  name: 'RateWithLabel',
  components: {
    StarRating,
  },
  props: {
    ratingLabel: {
      type: String,
      required: true,
    },
    ratingAvg: {
      type: Number,
      required: true,
    },
  },
  filters: {
    toDecimal(value) {
      let realVal = ''
      if (!isNaN(value) && value !== '') {
        realVal = parseFloat(value).toFixed(2)
      } else {
        realVal = '--'
      }
      return realVal
    },
  },
}
</script>
