<template>
  <PropertySection>
    <template v-slot:title v-if="isLandlord">
      {{ $t('property.landlordReviews') }}
    </template>
    <template v-slot:title v-else>
      {{ $t('property.propertyDetails.propertyReviews') }}
    </template>

    <template>
      <StarRateBox
        v-if="!isLandlord"
        :with-star-rating="true"
        :rate="reviewsRating"
        :reviews-count="reviewsCount"
        class="star-rate-box"
      />

      <b-row
        class="m-0 justify-content-between px-0"
        :class="{ 'col-lg-6': isLandlord }"
      >
        <b-col
          cols="12"
          md="6"
          class="d-inline-block p-0 review-sub-container"
          v-for="(reviewData, ratingLabel) in reviewsData"
          :key="ratingLabel"
        >
          <RateWithLabel :rating-label="ratingLabel" :rating-avg="reviewData" />
        </b-col>
      </b-row>
      <b-overlay :show="reviewLoading" rounded="sm" :opacity="1.0">
        <b-row class="review-card-row">
          <b-col
            cols="12"
            md="6"
            class="d-inline-block review-card p-0"
            v-for="(card, index) in propertyReviews"
            :key="index"
          >
            <ReviewCard
              :profile-picture="card.profile_picture"
              :user-name="card.user_name"
              :creation-date="card.creation_date"
              :note="card.note"
            />
          </b-col>
        </b-row>
      </b-overlay>
      <b-row
        class="review-card-row"
        v-if="propertyReviews && propertyReviews.length > 0"
      >
        <b-col
          cols="12"
          class="property-reviews-button"
          v-if="reviewsCount > numberReviewsShown"
        >
          <b-button @click="viewAllReviews">
            {{
              viewLess
                ? $t('common.viewLess')
                : $t('common.viewAll') + reviewsCount + $t('common.reviews')
            }}
          </b-button>
        </b-col>
      </b-row>
    </template>
  </PropertySection>
</template>
<script>
import PropertySection from '@/components/property-details/PropertySection'
import ReviewCard from '@/components/shared/ReviewCard'
import RateWithLabel from '@/components/shared/RateWithLabel'
import StarRateBox from '@/components/shared/StarRateBox'

export default {
  name: 'PropertyReviews',
  components: {
    PropertySection,
    ReviewCard,
    RateWithLabel,
    StarRateBox,
  },
  props: {
    propertyReviews: {
      type: Array,
      required: true,
    },
    reviewsData: {
      type: Object,
      required: true,
    },
    reviewsRating: {
      type: Number,
    },
    reviewsCount: {
      type: Number,
    },
    isLandlord: {
      type: Boolean,
    },
    reviewLoading: {
      type: Boolean,
    },
    numberReviewsShown: {
      type: Number,
    },
  },
  data() {
    return {
      viewLess: 0,
    }
  },
  computed: {
    reviewLength: function () {
      if (!this.propertyReviews) return 0
      return Object.keys(this.propertyReviews).length
    },
  },
  watch: {
    reviewLength(value) {
      if (value == this.reviewsCount) {
        this.viewLess = 1
      } else {
        this.viewLess = 0
      }
    },
  },
  methods: {
    viewAllReviews() {
      this.$emit('viewAllReviews', { viewLess: this.viewLess })
    },
  },
}
</script>
<style lang="scss" scoped>
.property-section {
  border: 0px;
  .review-card-row {
    justify-content: space-between;
    margin: 0;
  }
  .review-card {
    @include media-breakpoint-up(md) {
      max-width: 45%;
    }
    color: $mainTextColor;
    margin-bottom: 30px;
  }
  .property-reviews-button {
    text-align: center;
    button {
      background-color: $mainColorTransparent;
      color: $mainColorLight;
      border-color: $mainColorTransparent;
    }
  }
}

.star-rate-box {
  margin-bottom: 30px;
}
.review-sub-container {
  @include media-breakpoint-up(md) {
    max-width: 45%;
  }
}
</style>
