<template>
  <button class="btn btn-link text-primary" @click="editProperty">
    {{ $t('common.editProperty') }}
  </button>

</template>

<script>

import $axios from '~/helpers/axios-port'

export default {
  props: {
    propertyId: {
      required: true,
      type: String | Number,
    },
  },
  methods: {

    saveDataToSessionStorage(data, masterData){
      let city = masterData.rootLocations.filter((location)=>{
        if (location.location_id=== data.locations[0]){
          return location;
        }
      })[0];
      if (data.locations.length > 1){
        $axios.$get(`/getChildrenRoutes/${city.location_id}`).then((response)=>{
          let district = response.Data.rootLocations.filter((location)=>{
            if (location.location_id=== data.locations[1]){
              return location;
            }
          })[0];
          if (data.locations.length >2){
            $axios.$get(`/getChildrenRoutes/${district.location_id}`).then((response)=>{
              let neighbourhood = response.Data.rootLocations.filter((location)=>{
                if (location.location_id=== data.locations[2]){
                  return location;
                }
              })[0];


              let dataToCache = JSON.stringify({
                form1:{
                  city: city,
                  district: district,
                  neighbourhood: neighbourhood,
                  location_id: neighbourhood.location_id? neighbourhood.location_id:
                    district.location_id? district.location_id:
                      city.location_id? city.location_id:0,
                  street_name: data.street_name? data.street_name: '',
                },
                form2: {
                  bathrooms: data.bathroom,
                  bedrooms: data.bedroom,
                  furnishType: data.furnish_type_id,
                  propertyType: data.property_type_id,
                  bed_types: data.bed_types
                },
                form3: {
                  bathRoomAmenities:  data.amenities && data.amenities.Bathroom? data.amenities.Bathroom.map((amenity)=>{
                    return amenity.id;
                  }): [],
                  kitchenAmenities: data.amenities && data.amenities.Kitchen? data.amenities.Kitchen.map((amenity)=>{
                    return amenity.id;
                  }):[],
                  propertyAmenities: data.amenities && data.amenities.Property? data.amenities.Property.map((amenity)=>{
                    return amenity.id;
                  }):[],

                },
                form4: {
                  daily_price: data.daily_price,
                  description_en: data.description_en,
                  monthly_price: data.monthly_price,
                  name_en: data.property_name,
                  term: data.term
                },
                form5: {
                  default_image: data.default_image,
                  oldImages: data.old_images
                }
              });
              this.saveAnNavigate(dataToCache);
            });
          }else {
            let neighbourhood = {};


            let dataToCache = JSON.stringify({
              form1:{
                city: city,
                district: district,
                neighbourhood: neighbourhood,
                location_id: neighbourhood.location_id? neighbourhood.location_id:
                  district.location_id? district.location_id:
                    city.location_id? city.location_id:0,
                street_name: data.street_name? data.street_name: '',
              },
              form2: {
                bathrooms: data.bathroom,
                bedrooms: data.bedroom,
                furnishType: data.furnish_type_id,
                propertyType: data.property_type_id,
                bed_types: data.bed_types
              },
              form3: {
                bathRoomAmenities: data.amenities && data.amenities.Bathroom? data.amenities.Bathroom.map((amenity)=>{
                  return amenity.id;
                }) : [],
                kitchenAmenities: data.amenities && data.amenities.Kitchen? data.amenities.Kitchen.map((amenity)=>{
                  return amenity.id;
                }):[],
                propertyAmenities: data.amenities && data.amenities.Property? data.amenities.Property.map((amenity)=>{
                  return amenity.id;
                }):[],

              },
              form4: {
                daily_price: data.daily_price,
                description_en: data.description_en,
                monthly_price: data.monthly_price,
                name_en: data.property_name,
                term: data.term
              },
              form5: {
                default_image: data.default_image,
                images: data.old_images.map((imageUrl)=>{
                  return{
                    default: false,
                    image: imageUrl
                  }
                })
              }
            });
            this.saveAnNavigate(dataToCache);
          }
        });
      } else{
        let district = {};
        let neighbourhood = {};

        let dataToCache = JSON.stringify({
          form1:{
            city: city,
            district: district,
            neighbourhood: neighbourhood,
            location_id: neighbourhood.location_id? neighbourhood.location_id:
              district.location_id? district.location_id:
                city.location_id? city.location_id:0,
            street_name: data.street_name? data.street_name: '',
          },
          form2: {
            bathrooms: data.bathroom,
            bedrooms: data.bedroom,
            furnishType: data.furnish_type_id,
            propertyType: data.property_type_id,
            bed_types: data.bed_types
          },
          form3: {
            bathRoomAmenities:  data.amenities && data.amenities.Bathroom? data.amenities.Bathroom.map((amenity)=>{
              return amenity.id;
            }):[],
            kitchenAmenities:  data.amenities && data.amenities.Kitchen? data.amenities.Kitchen.map((amenity)=>{
              return amenity.id;
            }):[],
            propertyAmenities: data.amenities && data.amenities.Property? data.amenities.Property.map((amenity)=>{
              return amenity.id;
            }):[],

          },
          form4: {
            daily_price: data.daily_price,
            description_en: data.description_en,
            monthly_price: data.monthly_price,
            name_en: data.property_name,
            term: data.term
          },
          form5: {
            default_image: data.default_image,
            images: data.old_images.map((imageUrl)=>{
              return{
                default: false,
                image: imageUrl
              }
            })
          }
        });
        this.saveAnNavigate(dataToCache);
      }
    },
    saveAnNavigate(dataToCache){
      sessionStorage.setItem('addListing',dataToCache);
      window.location.href = '/properties/add/1?edit='+this.propertyId
    },
    editProperty() {
      $axios.$get(`/getPropMasterData`).then((response)=>{
        $axios.$get(`/user/properties/${this.propertyId}`).then((res)=>{
          this.saveDataToSessionStorage(res.Data, response.Data);
        });
      });
    },
  },
}
</script>

