<template>
  <PropertySection>
    <template v-slot:title>
      {{ $t('property.propertyDetails.neighborhoodTitle') }}
    </template>

    <template>
      <StarRateBox
        :withStarRating="true"
        :rate="neighborhoodRating"
        :reviews-count="neighborhoodReviewCount"
        class="star-rate-box"
      />
      <b-row class="m-0 justify-content-between review-container">
        <b-col
          cols="12"
          md="6"
          class="d-inline-block p-0 review-sub-container"
          v-for="(reviewData, ratingLabel) in neighbourReviewData"
          :key="ratingLabel"
        >
          <RateWithLabel
            :rating-label="ratingLabel"
            :rating-avg="reviewData"
          />
        </b-col>
        <b-col cols="12" class="p-0">
          <PropertyNearBy />
        </b-col>
      </b-row>
    </template>
  </PropertySection>
</template>
<style lang="scss" scoped>
.star-rate-box {
  margin-bottom: 30px;
}
.review-container {
  justify-content: space-between;
}
.review-sub-container {
  @include media-breakpoint-up(md) {
    max-width: 45%;
  }
}
</style>
<script>
import PropertySection from '@/components/property-details/PropertySection'
import RateWithLabel from '@/components/shared/RateWithLabel'
import PropertyNearBy from '@/components/property-details/PropertyNearBy'
import StarRateBox from '@/components/shared/StarRateBox'

export default {
  name: 'PropertyNeighborhood',
  components: {
    PropertySection,
    RateWithLabel,
    PropertyNearBy,
    StarRateBox,
  },
  props: {
    neighbourReviewData: {
      type: Object,
      required: true,
    },
    neighborhoodRating: {
      type: Number,
    },
    neighborhoodReviewCount: {
      type: Number,
    },
  },
}
</script>
