<template>
  <div>
    <b-modal
      ref="review-modal"
      v-bind="$attrs"
      centered
      hide-header
      hide-footer
      @hidden="onCloseModal"
    >
      <b-overlay :show="modalLoading">
        <header class="d-flex align-items-center justify-content-between">
          <h2 class="mb-0">
            {{ headerTitle }}
          </h2>
          <b-button size="sm" @click="onCloseModal" class="close-button">
            <svg-icon name="close-black" />
          </b-button>
        </header>
        <b-row class="m-0 justify-content-between px-0">
          <b-col
            cols="12"
            class="d-inline-block p-0 review-sub-container"
            v-for="(reviewData, ratingLabel) in reviewsData"
            :key="ratingLabel"
          >
            <RateWithLabel
              :rating-label="ratingLabel"
              :rating-avg="reviewData"
            />
          </b-col>
        </b-row>
      </b-overlay>
    </b-modal>
  </div>
</template>

<script>
import RateWithLabel from '@/components/shared/RateWithLabel'
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('PropertyModule')
export default {
  name: 'ReviewsModal',
  components: {
    RateWithLabel,
  },
  props: {
    headerTitle: {
      type: String,
    },
    reviewsData: {
      type: Array,
    },
  },
  computed: {
    ...mapState({
      visibility: (state) => state.reviewModal.visibility,
      modalLoading: (state) => state.reviewModal.loading,
    }),
  },
  watch: {
    visibility(newVal) {
      newVal
        ? this.$refs['review-modal'].show()
        : this.$refs['review-modal'].hide()
    },
  },
  beforeDestroy() {
    this.hideReviewModal()
  },
  methods: {
    ...mapActions(['hideReviewModal']),
    onCloseModal() {
      this.hideReviewModal()
    },
  },
}
</script>

<style lang="scss" scoped>
header {
  margin-bottom: 20px;
  h2 {
    font-size: 18px;
    font-weight: bold;
    color: $mainTextColor;
    margin-right: 18px;
  }
  .close-button {
    background: none;
    border: none;
    margin: 0px;
    padding: 0px;
    line-height: 1;
  }
  svg {
    width: 24px;
    height: 24px;
  }
}

::v-deep() .modal-dialog {
  width: 551px;
  margin: auto;
}

@include media-breakpoint-down(sm) {
  ::v-deep() .modal-dialog {
    width: 343px;
  }
}
</style>
